@import url(https://fonts.googleapis.com/css?family=Rancho&display=swap);


.group-card {
  color: white;
  background-color: var(--holpoint-red);
}

.group-card:hover,
.group-card:active,
.group-card:focus,
.group-card.active {
  opacity: 0.8;
}

.group-card {
  min-height: 300px;
}

.friend-image {
  border: 1px solid var(--holpoint-yellow);
}
.friend-image:hover,
.friend-image:visited,
.friend-image:link,
.friend-image:active,
.friend-image.active {
  text-decoration: none;
  border-width: 2px;
}
.friend-image.selected {
  border-width: 5px;
  border-color: var(--holpoint-green);
}

.idea-modal .modal-content {
  background-color: var(--holpoint-blue);
  color: #fff;
}

.idea-modal .card-body::-webkit-scrollbar-track {
  background: var(--holpoint-dark-blue);
}

.group-modal .modal-content {
  background-color: var(--holpoint-red);
  color: #fff;
}

.group-modal .card-body::-webkit-scrollbar-track {
  background: var(--holpoint-dark-red);
}

.activity-modal .modal-content {
  background-color: var(--holpoint-green);
  color: #fff;
}

.activity-modal .card-body::-webkit-scrollbar-track {
  background: var(--holpoint-dark-green);
}

/* modal body overflow */
.group-modal .card-body {
  overflow-y: auto;
}

.idea-modal .card-body {
  overflow-y: auto;
}

.activity-modal .card-body {
  overflow-y: auto;
}

/* modal size */
.modal-dialog,
.modal-content {
  height: 90%;
}

.modal-body {
  max-height: calc(100% - 120px);
}

.confirm-modal .modal-dialog,
.confirm-modal .modal-content {
  height: auto;
}

.idea-card {
  color: #fff;
  background-color: var(--holpoint-blue);
  margin-bottom: 10px;
}

.idea-card:hover,
.idea-card:active,
.idea-card:focus,
.idea-card.active {
  opacity: 0.8;
}

.idea-card .card-body {
  overflow-y: auto;
  height: 200px;
}

.card-body::-webkit-scrollbar {
  width: 0.25rem;
}

.card-body::-webkit-scrollbar-track {
  background: var(--holpoint-dark-blue);
}

.card-body::-webkit-scrollbar-thumb {
  background: #fff;
}

.idea-comment {
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
  background-color: var(--holpoint-dark-blue);
}

.activity-comment {
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
  background-color: var(--holpoint-dark-green);
}

.vote-panel {
  background-color: var(--holpoint-dark-blue);
  color: #ffffff;
  height: 50px;
  border-radius: 5px;
  margin-bottom: 0px;
  display: flex;
}

.hs {
  position: absolute;
  left: 0;
  width: 100%;
  display: flex;
  overflow-x: auto;
  /* min-height: 200px; */
}

.hs::-webkit-scrollbar {
  height: 0.5rem;
}

.hs::-webkit-scrollbar-track {
  background: #e6e6e6;
}

.hs::-webkit-scrollbar-thumb {
  background: var(--holpoint-green);
}

.column {
  border: 2px solid;
  border-radius: 5px;
  width: 250px;
  min-height: 400px;
  margin: 10px;
  padding: 10px;
  flex-grow: 1;
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
}

.title {
  margin: 0px 15px 0px 15px;
  font-size: 20px;
}

.activity-card {
  color: #fff;
  margin-bottom: 10px;
}

.activity-card:hover,
.activity-card:active,
.activity-card:focus,
.activity-card.active {
  opacity: 0.8;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.activity-url a:link,
.activity-url a:visited {
  font-style: italic;
  color: var(--holpoint-yellow) !important;
  text-decoration: none;
}

.attachment-card {
  border-radius: 5px;
  padding: 10px;
  margin: auto;
  margin-top: 5px;
  background-color: #e6e6e6;
  overflow: hidden;
}

.attachment-card a:link,
.attachment-card a:visited {
  color: #000 !important;
  text-decoration: none;
}

.forgot-password a:link,
.forgot-password a:visited {
  color: #000 !important;
  text-decoration: none;
}

:root {
  --holpoint-blue: #0099cc;
  --holpoint-dark-blue: #006080;
  --holpoint-red: #ff6b4f;
  --holpoint-dark-red: #6e1100;
  --holpoint-yellow: #ffcb31;
  --holpoint-green: #00ce83;
  --holpoint-dark-green: #006c44;
  --holpoint-light-green: #94ffd9;
  --holpoint-violet: #250c67;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #e6e6e6;
}

body::-webkit-scrollbar-thumb {
  background: #ff6b4f;
  background: var(--holpoint-red);
}

.text-with-newline {
  white-space: pre-line;
  word-wrap: break-word;
}

.profile-pic-small {
  border: 1px solid #ffcb31;
  border: 1px solid var(--holpoint-yellow);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  object-fit: cover;
}

.profile-pic-medium {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
}

.profile-pic-big {
  border: 3px solid #ffcb31;
  border: 3px solid var(--holpoint-yellow);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
}

